<template>
  <div class='shareBack'>
    <div class='vipImga'>
      <swiper :options="swiperOption" ref="mySwper">
        <swiper-slide v-for='(item, index) in img' :key='item.id'>
          <img :src='downloadUrl[index]'  class='shareBan'/>
        </swiper-slide>
      </swiper>
    </div>
    <div id='qrcode' style='display:none'></div>
  </div>
</template>

<script>
import QRcode from 'qrcodejs2'

export default {
  data () {
    return {
      swiperOption: {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        coverflowEffect: {
          rotate: 0,
          stretch: -50,
          depth: 80,
          modifier: 1,
          slideShadows: false
        },
        on: {
          slideChangeTransitionEnd: () => {
            let swiper = this.$refs.mySwper.swiper
            if (swiper) {
              this.realIndex = swiper.realIndex
            }
          }
        }
      },
      downloadUrl: [],
      img: [
        {
          id: 0,
          img: require('../../assets/share/a.png')
        },
        {
          id: 1,
          img: require('../../assets/share/b.png')
        },
        {
          id: 2,
          img: require('../../assets/share/c.png')
        }
      ],
      realIndex: 0,
      phone: ''
    }
  },
  components: {
  },
  watch: {
  },
  beforeCreate () {
  },
  created () {
    this.phone = JSON.parse(this.$stact.state.token)[0].phone
  },
  mounted () {
    this.qrcode()
  },
  methods: {
    qrcode () {
      let vm = this
      let qrcode = new QRcode('qrcode', {
        width: 200,
        height: 200, // 高度
        text: 'http://kadun.llyzf.cn:6442/lly-posp-proxy/card/card.html#/registered' + vm.phone
      })
      console.log(qrcode)
      vm.img.map(async (x, i) => {
        await vm.changeimg(i)
      })
    },
    changeimg (i) {
      let vm = this
      let imgsrcArray = [
        vm.img[i].img,
        document.querySelector('#qrcode canvas').toDataURL('image/png')
      ]
      let canvas = document.createElement('canvas')
      let ctx = canvas.getContext('2d')
      canvas.width = 740
      canvas.height = 1250
      ctx.fillStyle = '#f5f5f5'
      ctx.fillRect(0, 0, 740, 1250)
      let imglen = imgsrcArray.length
      function f (n) {
        if (n < imglen) {
          let img = new Image()
          img.crossOrigin = 'Anonymous' // 解决跨域问题
          img.onload = function () {
            if (n === 0) {
              ctx.drawImage(img, 0, 0, 740, 1250)
            } else {
              ctx.drawImage(img, 490, 1015, 220, 220)
            }
            f(n + 1)
          }
          img.src = imgsrcArray[n]
        } else {
          vm.downloadUrl.push('')
          vm.downloadUrl[i] = canvas.toDataURL('image/jpeg')
        }
      }
      f(0)
    }
  }
}
</script>

<style scoped>
  .swiper-slide {
    width: 10rem !important;
  }
</style>
